@font-face {
  font-family: Film P3 Bold;
  src: url("FilmP3Bold.e8cc015c.ttf");
}

:root {
  --primary-text-color: #757575;
  --accent-color: #c79002;
  --accent-hover-color: #f3c653;
  --primary-white-color: #fff;
  --primary-black-color: #494848;
  --dark-bg-color: #7a7a7a;
  --grey-bg-color: #7a7a7a;
  --light-bg-color: #bababa;
  --address-text-color: #e5e5e5;
  --hero-gradient-color: #000000a6;
  --logo-font: "Film P3 Bold", sans-serif;
  --primary-font: "Montserrat", sans-serif;
}

.visually-hidden {
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  list-style: none;
}

body {
  font-family: var(--primary-font);
  background-color: var(--primary-white-color);
  color: var(--primary-text-color);
  font-size: 16px;
  font-weight: 600;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@media screen and (width >= 1280px) {
  .section {
    padding-top: 94px;
    padding-bottom: 94px;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media screen and (width >= 414px) {
  .container {
    color: red;
    max-width: 414px;
  }
}

@media screen and (width >= 1024px) {
  .container {
    color: green;
    max-width: 1024px;
    padding-left: 47px;
    padding-right: 47px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    color: #00f;
    max-width: 1280px;
    padding-left: 85px;
    padding-right: 85px;
  }
}

.section-title {
  text-align: center;
  letter-spacing: .03em;
  color: var(--title-text-color);
  margin-bottom: 30px;
  font-size: 28px;
  font-weight: 700;
  line-height: 1.18;
}

@media screen and (width >= 1280px) {
  .section-title {
    margin-bottom: 50px;
    font-size: 36px;
    line-height: 1.17;
  }
}

.backdrop {
  opacity: 1;
  background: #0003;
  width: 100%;
  height: 100%;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  background: var(--primary-white-color);
  border-radius: 4px;
  width: 95%;
  max-height: 95%;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

@media screen and (width >= 414px) {
  .modal {
    width: 460px;
  }
}

@media screen and (width >= 1280px) {
  .modal {
    width: 528px;
  }
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(0);
}

.modal-close {
  background: var(--primary-white-color);
  cursor: pointer;
  border: 1px solid #0000001a;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-close .icon {
  fill: currentColor;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1);
}

.modal-close:hover .icon {
  fill: var(--accent-color);
}

.modal-title {
  text-align: center;
  letter-spacing: .03em;
  color: var(--title-text-color);
  margin-bottom: 12px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.5;
}

@media screen and (width >= 1280px) {
  .modal-title {
    line-height: 1.15;
  }
}

.header {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 20px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

@media screen and (width >= 1024px) {
  .header {
    padding-top: 24px;
    padding-bottom: 33px;
  }
}

.header-navigation {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header-logo {
  font-family: var(--logo-font);
  letter-spacing: .3rem;
  text-transform: uppercase;
  color: var(--primary-white-color);
  font-size: 36px;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
}

@media screen and (width >= 1024px) {
  .header-logo {
    font-size: 40px;
  }
}

.header-nav-list {
  justify-content: flex-end;
  align-items: center;
  gap: 6px;
  display: flex;
}

.header-nav-item:nth-child(2n):not(:last-child) {
  margin-right: 14px;
}

.header-nav-item-phone {
  letter-spacing: .2rem;
  border: 1px solid #0000;
  justify-content: space-between;
  align-items: center;
  padding: 8px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1;
  display: flex;
}

@media screen and (width >= 1024px) {
  .header-nav-item-phone {
    border: 1px solid var(--accent-color);
    color: var(--primary-white-color);
    border-radius: 2px;
  }
}

.header-nav-link {
  letter-spacing: .02rem;
  color: var(--accent-color);
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  text-decoration: none;
}

@media screen and (width >= 1024px) {
  .header-nav-link {
    font-size: 20px;
  }
}

.header-nav-link-phone {
  display: none;
}

@media screen and (width >= 1024px) {
  .header-nav-link-phone {
    letter-spacing: .2 rem;
    color: var(--primary-white-color);
    margin-left: 4px;
    font-size: 14px;
    font-weight: 400;
    line-height: 1;
    text-decoration: none;
    display: block;
  }
}

.icon-phone {
  width: 24px;
  height: 24px;
  fill: var(--accent-color);
}

.hero {
  padding-top: 181px;
  padding-bottom: 20px;
}

@media screen and (width >= 1024px) {
  .hero {
    padding-top: 188px;
    padding-bottom: 15px;
  }
}

@media screen and (width >= 1280px) {
  .hero {
    padding-top: 164px;
    padding-bottom: 64px;
  }
}

.hero-main {
  background: #666;
}
